.tarea-contenedor{
  width: 500px;
  min-height: 65px;
  background-color: #1b1b32;
  margin: 5px 0;
  padding: 8px 15px 8px 20px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  border-radius: 7px;
  border: 1px solid white;
  cursor: pointer;
}

.tarea-contenedor{
  background-color: blueviolet;
}

.tarea-texto{
  width: 100%;
  height: 100%;
  font-size: 20px;
  display: flex;
  align-items: center;
  overflow-wrap: anywhere;
}

.tarea-icono{
  width: 25px;
  height: 25px;
  margin: 5px;
  cursor: pointer;
}

.completada{
  border-color: #170C18;
  color: white;
  background-color: rgb(80, 3, 151) !important;
  text-decoration: line-through;
}